body {
	font-family: 'Roboto', sans-serif;
}
.container {
	max-width: 920px;
	margin-left: auto !important;
	margin-right: auto !important;
}

.autocomplete-dropdown-container {
	width: 100%;
	top: 100%;
	left: 0;
	position: absolute !important;
	z-index: 9;
	padding: 0 !important;
	box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
	max-height: 300px;
	overflow: auto;
}
.content-top {
    background: #243040;
    padding: 17px 20px;
}
.content-top .top {
	display: flex;
}
.content-left {
    width: 25%;
}
.content-right {
    width: 25%;
    margin: 0 12px;
}
.content-table {
	width: 50%;
}
.content-last {
    flex-grow: 1;
    width: 20%;
    text-align: center;
    border-left: 1px solid #344152;
    padding: 0 4px;
}
.content-last h6 {
    margin-bottom: 0;
    margin-top: 0;
    font-size: 14px;
}
.content-last .data {
    font-size: 14px;
    color: #4C5A6D;
}
.content-left h6 {
	font-size: 16px;
    color: #4C5A6D;
    font-weight: 600;
    margin-bottom: 10px;
    margin-top: 0;
}
.content-left h2 {
    font-size: 58px;
    font-weight: 300;
    color: #fff;
    margin: 0;
}
.content-right h5 {
    font-size: 16px;
    color: #fff;
    margin-bottom: 10px;
    margin-top: 0;
}
.forecast {
    color: #FFFFFF;
    display: flex;
}
.forecast-header {
    color: #4C5A6D;
    font-size: 16px;
    margin-bottom: 15px;
    margin-top: 15px;
}
.cities-adder {
    padding: 15px; 
    margin-bottom: 16px;
}
@media (max-width: 960px) {
    .aside-wrap {
        display: flex;
    }
    .cities-adder {
        width: 50%;
        flex-grow: 1;
        margin-bottom: 0 !important;
        margin-right: 20px;
    }
    .cities-list {
        width: 50%;
    }
    .cities-adder-field {
        width: 100%;
    }
}
@media (max-width: 540px) {
    .container {
        width: 100% !important;
        margin: 0 !important;
    }
    .cities-adder {
        margin-right: 0;
        width: 100%;
        margin-bottom: 16px !important;
    }
    .cities-list {
        width: 100%;
    }
    .aside-wrap {
        flex-wrap: wrap;
    }
    .content-top .top {
        flex-wrap: wrap;
    }
    .content-table {
        width: 100%;
        margin-top: 25px;
    }
    .content-table td {
        padding: 4px 10px 4px 10px !important;
    }
    .content-left {
        width: 50%;
    }
    .forecast {
        flex-wrap: wrap;
    }
    .content-last {
        width: 50%;
        margin-bottom: 10px;
    }
    .content-last:nth-child(odd) {
        border-left: none;
    }
}
